<template>
  <input type="text" class="datepicker" :value="value" @change="changeDate($event.target.value)" :placeholder="placeholder">
</template>

<script>
import M from 'materialize-css/dist/js/materialize.min.js'

export default {
  name: 'base-datepicker',
  model: {
    prop: 'value',
    event: 'change'
  },
  props: {
    value: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: ' '
    }
  },
  data () {
    return {
      picker: null,
      options: {
        autoClose: false,
        showClearBtn: true,
        format: 'yyyy-mm-dd',
        i18n: {
          cancel: '',
          clear: '',
          months: [
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            ''
          ],
          monthsShort: [
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            ''
          ],
          weekdays: [
            '',
            '',
            '',
            '',
            '',
            '',
            ''
          ],
          weekdaysShort: [
            '',
            '',
            '',
            '',
            '',
            '',
            ''
          ],
          weekdaysAbbrev: [
            '',
            '',
            '',
            '',
            '',
            '',
            ''
          ]
        }
      }
    }
  },
  methods: {
    changeDate (value) {
      this.$emit('change', value)
    }
  },
  mounted () {
    this.picker = M.Datepicker.init(this.$el, this.options)
  },
  destroyed () {
    this.picker.destroy()
  }
}
</script>
